@import "mixins";
@import "cores";

#about{
  padding-top: 100px;
}

.about-wrapper-all {
  width: 100%;
  height: auto;
  clear: both;
  float: left;
  background-color: #fff;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

section .title-holder {
  width: 100%;
  height: auto;
  clear: both;
  float: left;
  text-align: left;
  padding-bottom: 100px;
  position: relative;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;

  h3 {
    font-size: 50px;
    text-transform: uppercase;
    font-weight: 600;
    letter-spacing: -.5px;
    padding-left: 25px;
    line-height: 1;

    @media screen and(max-width: 480px) {
      padding-left: 0;
    }

    @media screen and(max-width: 768px) {
      font-size: 45px;
    }
  }

  span {
    font-size: 16px;
    font-family: "Montserrat", sans-serif;
    padding-left: 25px;

    @media screen and(max-width: 480px) {
      padding-left: 0;
    }
  }

  &:before {
    position: absolute;
    content: "";
    width: 6px;
    height: 72px;
    background-color: $cor-secundaria;
    top: 2px;
  }
}

.about-wrap {
  width: 100%;
  height: auto;
  clear: both;
  float: left;
  padding-bottom: 122px;
  position: relative;
  margin-top: -6px;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;

  @media screen and(max-width: 1040px) {
    flex-direction: column;
  }

  .author-wrap {
    width: 100%;
    height: auto;
    clear: both;
    float: left;
    position: relative;
    display: flex;
    align-items: center;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;

    .leftbox {
      width: 35%;
      float: left;
      height: auto;
      padding-right: 40px;
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box;

      @media screen and(max-width: 1200px) {
        display: none;
      }

      @media screen and(max-width: 1400px) {
        padding-right: 0;
      }

      .about-image-wrap {
        position: relative;

        .image {
          position: absolute;
          z-index: 2;

          .inner {
            position: absolute;
            top: 0px;
            bottom: 0px;
            left: 0px;
            right: 0px;
            background-repeat: no-repeat;
            background-size: cover;
            background-position: center;
          }
        }

        .border .inner {
          position: absolute;
          top: 25px !important;
          bottom: -50px !important;
          left: 25px !important;
          right: -25px !important;
          border: 10px solid $cor-primaria;
        }

        img {
          opacity: 0;
        }
      }
    }

    .rightbox {
      width: 65%;
      float: right;
      padding: 0 0 0 65px;
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box;

      @media screen and(max-width: 1200px) {
        padding-left: 0;
        width: 100%;
      }

      .mini-title-holder {
        width: 100%;
        height: auto;
        clear: both;
        float: left;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;

        h4 {
          line-height: 1;
          margin-bottom: 18px;
        }
      }

      .definition {
        margin-bottom: 15px;

        p {
          color: #333;

          strong {
            color: $cor-secundaria;
          }
        }
      }

      .about-short-contact-wrap {
        width: 100%;
        height: auto;
        clear: both;
        float: left;
        margin-bottom: 13px;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;

        ul {
          margin: 0px;
          list-style-type: none;

          li {
            margin: 0px 0px 10px 0px;
            width: 50%;
            float: left;

            @media screen and (max-width: 768px) {
              width: 100%;
            }

            label {
              font-family: "Montserrat";
              font-weight: 700;
              font-size: 13px;
              color: #000;
              min-width: 85px;
              display: inline-block;
            }

            a {
              text-decoration: none;
              color: #000;
              position: relative;
              -webkit-transition: all .3s ease;
              -moz-transition: all .3s ease;
              -ms-transition: all .3s ease;
              -o-transition: all .3s ease;
              transition: all .3s ease;

              &:hover {
                color: $cor-secundaria;
              }

            }
          }
        }
      }

      .buttons-wrap {
        width: 100%;
        height: auto;
        clear: both;
        float: left;

        -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
        -moz-box-sizing: border-box; /* Firefox, other Gecko */
        box-sizing: border-box; /* Opera/IE 8+ */

        ul {
          margin: 0px;
          list-style-type: none;

          li {
            margin: 0px 30px 10px 0px;
            float: left;

            &:last-child {
              margin-right: 0px;
            }

            a {
              text-decoration: none;
              color: #fff;
              background-color: $cor-secundaria;
              font-weight: 600;
              font-family: "Montserrat", sans-serif;
              padding: 12px 35px;
              border-radius: 3px;
              display: inline-block;
              position: relative;
              overflow: hidden;

              &:before {
                position: absolute;
                content: "";
                top: 0px;
                height: 100%;
                left: -25%;
                width: 0%;
                background-color: $cor-primaria;
                transform: skew(50deg);
                transition-duration: .6s;
                z-index: 1;
              }

              &:hover{
                cursor: pointer;
              }

              &:hover:before {
                width: 180%;
              }

              span {
                position: relative;
                z-index: 2;
              }
            }
          }
        }
      }
    }
  }
}