@import "cores";

.typing{
  display: inline-block;
}

.efaGep{
  color: #fff !important;
}

.hero-wrap{
  width: 100%;
  height: auto;
  clear: both;
  float: left;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;

  .box-wrap{
    width: 100%;
    height: auto;
    clear: both;
    float: left;
    position: relative;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;

    .bg-wrap{
      position: absolute;
      top: 0px;
      bottom: 0px;
      left: 0px;
      right: 0px;
      z-index: 10;

      .overlay-color{
        position: absolute;
        top: 0px;
        bottom: 0px;
        left: 0px;
        right: 0px;
        z-index: 20;

        &.hero{
          background-color: $cor-primaria-80;
        }
      }
    }

    .overlay-image{
      position: absolute;
      top: 0px;
      bottom: 0px;
      left: 0px;
      right: 0px;
      z-index: 15;
      background-size: cover;
      background: no-repeat center;

      img{
        height: 100%;
        width: 100%;
        object-fit: cover;
      }
    }



    .content{
      width: 100%;
      height: auto;
      clear: both;
      float: left;
      position: relative;
      z-index: 25;
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box;

      .inner-content{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        z-index: 2;
        text-align: center;
        width: 100%;
        padding: 0px 20px;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;

        .image-wrap{
          width: 200px;
          height: 200px;
          display: inline-block;
          margin-bottom: 38px;

          img{
            border-radius: 100%;
            border: 8px solid rgba(255,255,255,.5);
          }
        }

        .name-holder{
          h3{
            font-family: Montserrat, sans-serif;
            text-transform: uppercase;
            color: #fff;
            font-size: 50px;
            font-weight: 300;

            span{
              font-weight: 500;
              color: $cor-secundaria;
            }
          }
        }

        .text-typing{
          width: 100%;
          height: auto;
          float: left;
          -webkit-box-sizing: border-box;
          -moz-box-sizing: border-box;
          box-sizing: border-box;

          p{
            color: #fff;
            font-size: 24px;
            font-family: Montserrat, sans-serif;

            .animation-text-word{
              font-weight: 600;
              line-height: 1;
              font-family: Montserrat, sans-serif;
            }
          }
        }
      }

      &.hero{
        height: 100vh;
      }
    }
  }
}