@import "mixins";
@import "cores";

.title-holder.portfolio{padding-bottom: 90px;}


.portfolio-wrapper-all{
  width: 100%;
  height: auto;
  clear: both;
  float: left;
  padding-bottom: 70px;
  background-color: #fff;

  -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
  -moz-box-sizing: border-box; /* Firefox, other Gecko */
  box-sizing: border-box; /* Opera/IE 8+ */

  .button{text-align: center;}
}

.portfolio-wrap{
  width: 100%;
  height: auto;
  clear: both;
  float: left;

  -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
  -moz-box-sizing: border-box; /* Firefox, other Gecko */
  box-sizing: border-box; /* Opera/IE 8+ */
}

.portfolio-filter{
  width:100%;
  height: auto;
  text-align:left;
  list-style-type:none;
  margin: 0px 0px 13px 0px;
  display: inline-block;

  -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
  -moz-box-sizing: border-box; /* Firefox, other Gecko */
  box-sizing: border-box; /* Opera/IE 8+ */
}

.portfolio-filter li{
  display:inline-block;
  margin:0px 40px 0px 0px;
}

.portfolio-filter li:last-child{margin-right: 0px;}
.portfolio-filter li a{
  text-decoration:none;
  font-size:14px;
  font-weight:700;
  color:#000;
  display: inline-block;
  margin-bottom: 10px;
  position: relative;

  -webkit-transition: all .1s ease;
  -moz-transition: all .1s ease;
  -ms-transition: all .1s ease;
  -o-transition: all .1s ease;
  transition: all .1s ease;
}
.portfolio-filter li a.current{color: $cor-secundaria}

/* PORTFOLIO LIST */

.portfolio-list{
  list-style-type:none;
  margin-left: -30px;

  -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
  -moz-box-sizing: border-box; /* Firefox, other Gecko */
  box-sizing: border-box; /* Opera/IE 8+ */
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));

  & > li{
    float:left;
    position:relative;
    margin: 0px 0px 30px 0px;
    padding-left: 30px;

    -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
    -moz-box-sizing: border-box; /* Firefox, other Gecko */
    box-sizing: border-box; /* Opera/IE 8+ */
  }

  li img{min-width: 100%;}
}

.portfolio-titles {
  white-space: nowrap;
  background: #ffffff;
  font-size: 20px;
  font-weight: 700;
  padding: 5px 15px;
  color: #000000;
  position: fixed;
  z-index: 1000;
  opacity: 0;
  visibility: hidden;
}
.portfolio-titles.visible {
  opacity: 1;
  visibility: visible;
}
.portfolio-titles .work--cat {
  position: absolute;
  background: #ffffff;
  top: 100%;
  left: 0;
  margin-top: -5px;
  font-family: "Montserrat";
  font-size: 12px;
  font-weight: 400;
  padding: 5px 15px;
}
.entry{position: relative;}
.portfolio-image-main{
  position: absolute;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
