@import "assets/scss/_cabecalho.scss";
@import "assets/scss/_sobre.scss";
@import "assets/scss/_skills.scss";
@import "assets/scss/_hero.scss";
@import "assets/scss/_portfolio.scss";
@import "assets/scss/_popup.scss";

* {
  background: transparent;
  border: 0;
  margin: 0;
  padding: 0;
  vertical-align: baseline;
  outline: none;
}

body {
  font-family: Montserrat, sans-serif;
  font-size: 14px;
  line-height: 1.8;
  letter-spacing: 0.5px;
  font-weight: 400;
  word-wrap: break-word;
}

.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.conteudo {
  width: 100%;
  height: auto;
  float: left;
  padding-left: 300px;
  -webkit-transition: all .3s ease;
  -moz-transition: all .3s ease;
  -ms-transition: all .3s ease;
  -o-transition: all .3s ease;
  transition: all .3s ease;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;

  &.full {
    padding-left: 0px;
  }
  &.opened {
    padding-left: 0px;
  }
  .rightpart_inner {
    width: 100%;
    height: auto;
    clear: both;
    float: left;

    -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
    -moz-box-sizing: border-box; /* Firefox, other Gecko */
    box-sizing: border-box; /* Opera/IE 8+ */
  }

  @media screen and (max-width: 1040px) {
    padding-left: 0px;
  }
}

section {
  width: 100%;
  height: auto;
  clear: both;
  float: left;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

img {
  vertical-align: middle;
  max-width: 100%;
}

.container {
  max-width: 1250px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  height: auto;
  padding: 0 40px;
  position: relative;
  clear: both;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;

  @media screen and (max-width: 1040px) {
    padding: 0 20px;
  }

  @media screen and (max-width: 480px) {
    padding: 0 10px;
  }
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 500;
  line-height: 1;
  font-family: "Montserrat", sans-serif;
}

h1 {
  font-size: 45px;
}

h2 {
  font-size: 36px;
}

h3 {
  font-size: 30px;
}

h4 {
  font-size: 24px;
}

h5 {
  font-size: 20px;
}

h6 {
  font-size: 16px;
}

.clearfix {
  *zoom: 1;
  clear: both;

  &:before {
    display: table;
    line-height: 0;
    content: "";
  }

  &:after {
    display: table;
    line-height: 0;
    content: "";
  }
}