@import "mixins";
@import "cores";

.skills-wrap {
  background-color: #f6f7fd;
  padding: 90px 0;
  margin-bottom: 100px;

  &, .inner-wrap {
    width: 100%;
    height: auto;
    clear: both;
    float: left;

    -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
    -moz-box-sizing: border-box; /* Firefox, other Gecko */
    box-sizing: border-box; /* Opera/IE 8+ */
  }

  .inner-wrap {
    display: flex;
  }

  .leftbox {
    width: 35%;
    height: auto;
    clear: both;
    float: left;
    padding-right: 0;

    -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
    -moz-box-sizing: border-box; /* Firefox, other Gecko */
    box-sizing: border-box; /* Opera/IE 8+ */

    .mini-title-holder{
      width: 100%;
      height: auto;
      clear: both;
      float: left;

      -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
      -moz-box-sizing: border-box; /* Firefox, other Gecko */
      box-sizing: border-box; /* Opera/IE 8+ */

      &.contact h4{margin-bottom:30px;}

      h4{
        line-height: 1;
        margin-bottom: 18px;
      }
    }
  }

  .rightbox {
    width: 65%;
    height: auto;
    clear: both;
    float: right;
    padding-left: 65px;

    -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
    -moz-box-sizing: border-box; /* Firefox, other Gecko */
    box-sizing: border-box; /* Opera/IE 8+ */

    .progress-bar-wrap-total {
      width: 100%;
      height: auto;
      clear: both;
      float: left;
      margin-bottom: 10px;
      position: relative;
      margin-top: -4px;

      -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
      -moz-box-sizing: border-box; /* Firefox, other Gecko */
      box-sizing: border-box; /* Opera/IE 8+ */

      .progress-wrap {
        &[data-size="big"] .progress .bar-bg {
          height: 40px;
        }
        &[data-size="small"] .progress .bar-bg {
          height: 5px;
        }
        &[data-round="a"] .progress .bar-bg,
        &[data-round="a"] .progress .bar {
          -webkit-border-radius: 2px;
          -moz-border-radius: 2px;
          border-radius: 2px;
        }

        &[data-round="b"] .progress .bar-bg,
        &[data-round="b"] .progress .bar {
          -webkit-border-radius: 5px;
          -moz-border-radius: 5px;
          border-radius: 5px;
        }

        &[data-round="c"] .progress .bar-bg,
        &[data-round="c"] .progress .bar {
          -webkit-border-radius: 50px;
          -moz-border-radius: 50px;
          border-radius: 50px;
        }

        .progress {
          width: 100%;
          margin-bottom: 16px;

          &:last-child {
            margin-bottom: 0;
          }
          & > span {
            margin: 0 0 3px 0;
            width: 100%;
            display: block;
            text-align: left;
          }

          span {

            &.label{
              font-size: 13px;
              font-weight: 700;
              color: #000;
              font-family: "Montserrat", sans-serif;

              .experience {
                font-size: 12px;
                font-weight: 600;
              }
            }

            &.number{
              float:right;
              font-size:13px;
              color:#000;
              font-weight: 700;
              font-family: "Montserrat", sans-serif;
            }
          }

          .bar-bg{
            background:rgba(0,0,0,.1);
            width:100%;
            min-width:100%;
            position:relative;
            height:20px;

            .bar-wrap {
              width: 0;
              height: 100%;

              &.open{
                -webkit-animation: expand 3s cubic-bezier(0.165, 0.840, 0.440, 1.000);  /* Safari 4+ */
                -moz-animation:    expand 3s cubic-bezier(0.165, 0.840, 0.440, 1.000);  /* Fx 5+ */
                animation:         expand 3s cubic-bezier(0.165, 0.840, 0.440, 1.000);  /* IE 10+ */
                width:100%;
              }
            }

            .tm-bar{
              height:100%;
              background:#333;
              width:0;
              overflow:hidden;
            }
          }
        }

        &[data-strip="on"] .tm-progress .tm-bar{
          background-image: linear-gradient(-45deg, rgba(255, 255, 255, 1) 25%, transparent 25%, transparent 50%, rgba(255, 255, 255, 1) 50%, rgba(255, 255, 255, 1) 75%, transparent 75%, transparent);

          -webkit-animation: bar-move 4s linear infinite;
          -moz-animation: bar-move 4s linear infinite;
          animation: bar-move 4s linear infinite;
          background-size: 50px 50px;
        }
      }
    }
  }
}

@-webkit-keyframes bar-move {0%{background-position: 0 0;} 100% {background-position: 50px 50px;}}
@-moz-keyframes bar-move {0%{background-position: 0 0;} 100% {background-position: 50px 50px;}}
@keyframes bar-move {0%{background-position: 0 0;} 100% {background-position: 50px 50px;}}

/*expand width animation*/
@-webkit-keyframes expand {0%{ width:0%; } 100%{ width:100%; }}
@-moz-keyframes expand {0%{ width:0%; } 100%{ width:100%; }}
@keyframes expand {0%{ width:0%; } 100%{ width:100%; }}

