@import "mixins";
@import "cores";

.cabecalho_left{
  width: 300px;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  background-color: $cor-primaria;
  z-index: 100;
  @include prop(transition, all .3s ease);

  &.fechado{
    left: -300px;
  }

  .logo{
    background-color: $cor-primaria;
    padding: 50px 20px;
    width: 100%;
    float: left;
    text-align: center;
    margin-bottom: 50px;
    border-bottom: 1px solid rgba(255,255,255,.1);
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;

    .lang{
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      margin: auto;
      width: 200px;

      a{
        height: 30px;

        img{
          width: 24px;
          height: 24px;
          filter: grayscale(1);
          transition: filter 100ms ease;

          &:hover{
            filter: grayscale(0);
          }
          &.active{
            filter: grayscale(0);
          }
        }
      }
    }
  }

  .lista_menu{
    width: 100%;
    height: auto;
    clear: both;
    float: left;
    padding: 0 20px 0 60px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;

    ul{
      margin: 0;
      list-style-type: none;

      li{
        margin: 0;

        &:last-child{ margin-bottom: 0 }

        a{
          text-decoration: none;
          color: #fff;
          font-family: Montserrat;
          font-size: 16px;
          font-weight: 500;
          text-transform: uppercase;
          position: relative;
          display: inline-block;
          padding-bottom: 19px;
          @include prop(transition, all .3s ease);

          &:before{
            position: absolute;
            content: "";
            height: 2px;
            width: 0px;
            background-color: $cor-secundaria;
            top: 14px;
            transform: translateY(-50%);
            left: 100%;
            margin-left: 10px;
            @include prop(transition, all .3s ease);
          }

          &:hover{
            color: $cor-secundaria;
            &:before{width: 35px}
          }
        }
      }
    }
  }

  .social{
    position: absolute;
    left: 60px;
    bottom: 38px;
  }

  .fechar{
    position: absolute;
    right: 0;
    bottom: 20%;
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    line-height: 30px;
    background-color: $cor-secundaria;
    text-decoration: none;
    z-index: 200;
    @include prop(transition, all .3s ease);

    img{
      width: 20px;
      height: 20px;
      align-self: center;
      transition: transform 200ms ease;
    }

    &.fechado{
      right: -30px;

      img{
        transform: rotate(180deg);
      }
    }
  }

  @media screen and (max-width:1040px){
    left: -300px;
  }
}

.cabecalho_mobile{
  width: 100%;
  height: auto;
  position: fixed;
  z-index: 11111111;
  left: 0px;
  top: 0px;
  display: none;

  .main{
    width: 100%;
    height: auto;
    clear: both;
    float: left;
    padding: 30px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: $cor-primaria;
    border-bottom: 1px solid rgba(255,255,255,.1);
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
  }

  .nav{
    width: 100%;
    height: auto;
    max-height: 0;
    clear: both;
    float: left;
    overflow: hidden;
    background-color: $cor-primaria-95;
    border-bottom: 1px solid rgba(0,0,0,.09);
    border-top: 1px solid rgba(0,0,0,.09);
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    transition: max-height 500ms ease;

    .mob_menu{
      width: 100%;
      height: 100%;
      clear: both;
      float: left;
      padding: 30px 20px;
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box;

      ul{
        margin: 0;
        list-style-type: none;

        li{
          margin: 0 0 5px 0;

          a{
            text-decoration: none;
            color: #fff;
            font-family: "Montserrat";
            font-weight: 600;
            font-size: 19px;
            text-transform: uppercase;
          }
          .lang{
            flex-direction: row;
            justify-content: space-between;
            margin: auto;
            width: 200px;
            display: flex;

            a{
              height: 30px;

              img{
                width: 24px;
                height: 24px;
                filter: grayscale(1);
                transition: filter 100ms ease;

                &:hover{
                  filter: grayscale(0);
                }
                &.active{
                  filter: grayscale(0);
                }
              }
            }
          }
        }
      }
    }

    &.is-active{
      max-height: 320px;
    }

    @media screen and (max-width:1040px){
      display: block;
    }
  }

  @media screen and (max-width:1040px){
    display: block;
  }
}

.trigger{width: auto;}
.trigger .hamburger{
  padding: 15px 15px;
  display: inline-block;
  cursor: pointer;
  transition-property: opacity, filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;
  font: inherit;
  color: inherit;
  text-transform: none;
  background-color: transparent;
  border: 0;
  margin: 0;
  overflow: visible;
}
.trigger .hamburger-box{
  width: 30px;
  height: 18px;
  display: inline-block;
  position: relative;
}
.trigger .hamburger-inner{
  display: block;
  top: 50%;
  margin-top: -2px;
}
.trigger .hamburger-inner, .hamburger-inner::before, .hamburger-inner::after{
  width: 30px;
  height: 2px;
  background-color: #333333;
  position: absolute;
  transition-property: transform;
  transition-duration: 0.15s;
  transition-timing-function: ease;

  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  border-radius: 4px;
}
.trigger .hamburger-inner::before,
.trigger .hamburger-inner::after{
  content: "";
  display: block;
}
.trigger .hamburger-inner::before{top: -8px;}
.trigger .hamburger-inner::after{ bottom: -10px;}
.trigger .hamburger--collapse-r .hamburger-inner{
  top: auto;
  bottom: 0;
  transition-duration: 0.13s;
  transition-delay: 0.13s;
  transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
}
.trigger .hamburger--collapse-r .hamburger-inner::after{
  top: -16px;
  transition: top 0.2s 0.2s cubic-bezier(0.33333, 0.66667, 0.66667, 1), opacity 0.1s linear;
}
.trigger .hamburger--collapse-r .hamburger-inner::before{
  transition: top 0.12s 0.2s cubic-bezier(0.33333, 0.66667, 0.66667, 1), transform 0.13s cubic-bezier(0.55, 0.055, 0.675, 0.19);
}
.trigger .hamburger--collapse-r.is-active .hamburger-inner{
  transform: translate3d(0, -10px, 0) rotate(45deg);
  transition-delay: 0.22s;
  transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
}
.trigger .hamburger--collapse-r.is-active .hamburger-inner::after{
  top: 0;
  opacity: 0;
  transition: top 0.2s cubic-bezier(0.33333, 0, 0.66667, 0.33333), opacity 0.1s 0.22s linear;
}
.trigger .hamburger--collapse-r.is-active .hamburger-inner::before{
  top: 0;
  transform: rotate(90deg);
  transition: top 0.1s 0.16s cubic-bezier(0.33333, 0, 0.66667, 0.33333), transform 0.13s 0.25s cubic-bezier(0.215, 0.61, 0.355, 1);
}
.trigger .hamburger{
  padding: 0px;
  height: 100%;
  display: table-cell;
  vertical-align: middle;
}
.trigger .hamburger-box{display: block;}
.trigger .hamburger .hamburger-inner::before,
.trigger .hamburger .hamburger-inner::after,
.trigger .hamburger .hamburger-inner{
  background-color: #fff;
  width: 30px;

  -webkit-transition: all .3s ease;
  -moz-transition: all .3s ease;
  -ms-transition: all .3s ease;
  -o-transition: all .3s ease;
  transition: all .3s ease;
}