@import "mixins";
@import "cores";

.popup-share-wrap {
  display: none;
}

::-webkit-scrollbar {display:none;}

#popup-blog {
  opacity: 0;
  visibility: hidden;
  height: 100vh;
  width: 100%;
  position: fixed;
  background-color: rgba(255, 255, 255, 1);
  padding: 50px 0;
  z-index: 99999999;

  -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
  -moz-box-sizing: border-box; /* Firefox, other Gecko */
  box-sizing: border-box; /* Opera/IE 8+ */

  -webkit-transition: all .2s ease;
  -moz-transition: all .2s ease;
  -ms-transition: all .2s ease;
  -o-transition: all .2s ease;
  transition: all .2s ease;

  &.opened {
    opacity: 1;
    visibility: visible;
  }

  .definitions-wrap {
    width: 100%;
    float: left;
    height: 100%;
  }

  .full-def {
    display: block;
    margin-bottom: 20px;
    width: 100%;
    float: left;

    p {
      display: inline-block;
    }
  }

  .definition {
    display: none;
  }

  .inner-popup {
    width: 100%;
    float: left;
    clear: both;
    position: relative;
    overflow-y: auto;
    outline: none;
    height: 100vh;
  }

  .date-wrap p {
    color: #333;
    font-family: "Montserrat", sans-serif;
    font-weight: 600;

    a {
      color: #333;
      text-decoration: none;
      text-transform: uppercase;
      position: relative;
      margin-left: 23px;

      -webkit-transition: all .3s ease;
      -moz-transition: all .3s ease;
      -ms-transition: all .3s ease;
      -o-transition: all .3s ease;
      transition: all .3s ease;

      &:hover {
        color: $cor-secundaria;
      }
    }
  }

  .image-wrap {
    margin-bottom: 45px;
    position: relative;
    width: 100%;
    float: left;

    .big {
      display: block;
      min-height: 250px;
    }

    .small {
      display: none;
    }

    .news-image {
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      width: 100%;
      height: 45vw;
      background: no-repeat center;
      background-size: cover;
    }
  }

  .date-wrap {
    margin-bottom: 16px;
    width: 100%;
    float: left;

    p a:before {
      position: absolute;
      content: "";
      width: 10px;
      height: 1px;
      background-color: #333;
      top: 50%;
      right: 100%;
      margin-right: 8px;
    }
  }

  .title-holder {
    margin-bottom: 18px;
    width: 100%;
    float: left;

    h3 {
      line-height: 1.4;
      font-weight: 600;
      font-family: "Montserrat", sans-serif;

      a {
        text-decoration: none;
        color: #000;
        font-size: 22px;
        display: block;
      }
    }
  }

  .definition p {
    color: #333;
  }

  .read-more {
    display: none;
  }

  .popup-share-wrap {
    display: block;
    width: 100%;
    height: auto;
    clear: both;
    float: left;
    padding-bottom: 5px;

    -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
    -moz-box-sizing: border-box; /* Firefox, other Gecko */
    box-sizing: border-box; /* Opera/IE 8+ */

    ul {
      margin: 0;
      list-style-type: none;

      li {
        display: inline-block;
        margin: 0 15px 0 0;

        a {
          text-decoration: none;
          color: #000;
          font-size: 15px;
          position: relative;
          font-family: "Montserrat", sans-serif;
          font-weight: 600;

          -webkit-transition: all .3s ease;
          -moz-transition: all .3s ease;
          -ms-transition: all .3s ease;
          -o-transition: all .3s ease;
          transition: all .3s ease;

          &:hover {
            color: $cor-secundaria;
          }
        }
      }
    }
  }

  .close {
    position: absolute;
    top: 30px;
    right: 40px;
    width: 50px;
    height: 50px;
    cursor: pointer;
    display: block;
    z-index: 111111111;

    @media (max-width: 1200px){
      right: 0;
    }

    a {
      position: relative;
      display: block;
      height: 30px;
      margin-right: 10px;
      margin-top: 12px;

      &:before {
        position: absolute;
        content: "";
        width: 2px;
        height: 20px;
        background-color: rgba(0, 0, 0, 1.00);
        top: 5px;
        right: 13px;
        transform: rotate(45deg);
      }

      &:after {
        position: absolute;
        content: "";
        width: 2px;
        height: 20px;
        background-color: rgba(0, 0, 0, 1.00);
        top: 5px;
        right: 13px;
        transform: rotate(135deg);
      }
    }
  }
}